@import "../variables.scss";

nav#title{
    position: fixed;
    top: 0;
    z-index: 2;

    width: 100%;
    height: $mobileNavHeight;
    color: $color_6;

    
    ul#base{
        a{
            text-decoration: none;
            color: $color_6;
        }
        background-color: $color_1;
        display: flex;
        align-items: center;
        height: $mobileNavHeight;
        
        #logo{
            width: 4em; height: 4em;
            
            border-radius: 50%;
            margin: 0.5em;
    
            background-image: url("/Images/site/favicon.jpg");
            background-size: cover;
            background-position: center;
        }
        .gap{
            width: 100%;
        }
        #mobileMenuButton{
            width: 3em; height: 3em;
            margin: 1em;
            background-image: url("/Images/site/bars.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: 1em;
            
            @media screen and (min-width: $mobileWidth){        
                display: none;
            }
        }
    }
    
    ul#lv1{
        display: none;
        flex-shrink: 0;
        margin: 1em;
        
        @media screen and (min-width: $mobileWidth){        
            display: flex;
        }
        li{
            position: relative;
            text-align: center;
            .lv2{
                display: none;
                width: 10em;
                position: absolute;
                right: 0;
                right: calc((100% / 2) - 5em);
            
                
                top: 3em;
                bottom: initial;
                &.up{
                    top: initial;
                    bottom: 3em;
                }
            }
            
            &:hover{
                .lv2{
                    display: block;
                }
            }
        }
    }

    &.activeMenu{
        @media screen and (max-width: $mobileHideLogo){
            ul#lv1{
                width: calc(100% - 6em);
            }
        }

        //***********MENU FIX**************
        @media screen and (max-width: 470px){
            ul#lv1{
                width: calc(100% - 2em);
                >a{
                    width: 100%;
                }
            }
            .gap, #mobileMenuButton{
                display: none;
            }
        }
        //*************************

        #mobileMenuButton{
            margin-left: 0 !important;
            background-image: url("/Images/site/close.svg") !important;
        }

        #logo{
            @media screen and (max-width: $mobileHideLogo){        
                display: none;
            }
        }
        #siteName{
            @media screen and (max-width: $mobileHideTitle){        
                display: none;
            }
        }
        ul#lv1{
            display: flex;
        }
    }
}